.type-program {
  display: flex;
  flex-wrap: wrap;
  background-color: #f2f2f2;
  padding-top: 24px;
  padding-bottom: 10px;
  padding-right: 21px;
  padding-left: 20px;

  &--next {
    margin-top: 20px;
  }

  @media (min-width: @tablet-width) {
    display: block;
    min-height: 371px;
    padding-top: 69px;
    padding-left: 51px;

    &--next {
      margin-top: 30px;
    }
  }

  @media (min-width: @desktop-width) {
    display: flex;
    width: 47%;
    padding-top: 46px;
    padding-bottom: 39px;

    &--next {
      margin-top: 0;
      padding-right: 30px;
    }
  }
}

.type-program__icon-wrapper {
  width: 50px;
  height: 50px;
  margin-right: 19px;
  background-color: #68b738;
  border-radius: 50%;

  @media (min-width: @tablet-width) {
    width: 200px;
    height: 200px;
    margin-top: 20px;
    margin-right: 40px;
    margin-left: 15px;
    float: right;
  }

  @media (min-width: @desktop-width) {
    width: 100px;
    height: 100px;
    margin-top: 0;
    margin-left: 0;
    margin-right: 60px;
  }
}

.type-program__icon-wrapper-mod {
  @media (min-width: @tablet-width) {
    margin-left: 52px;
    margin-right: 40px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 2px;
    margin-left: 0;
    margin-right: 60px;
  }
}

.type-program__icon-cat-slim {
  margin-top: -2.5px;
  margin-left: 8px;
  width: 35px;
  height: 49px;

  @media (min-width: @tablet-width) {
    width: 141px;
    height: 194px;
    margin-top: -10px;
    margin-left: 29px;
  }

  @media (min-width: @desktop-width) {
    width: 71px;
    height: 97px;
    margin-top: -6px;
    margin-left: 15px;
  }
}

.type-program__icon-cat-pro {
  margin-left: -9px;
  margin-top: 10px;
  width: 67px;
  height: 28px;

  @media (min-width: @tablet-width) {
    width: 268px;
    height: 113px;
    margin-top: 43px;
    margin-left: -35px;
  }

  @media (min-width: @desktop-width) {
    width: 134px;
    height: 57px;
    margin-top: 20px;
    margin-left: -19px;
  }
}

.type-program__title {
  flex: 1;
  text-transform: uppercase;
  font-weight: 400;
  color: #111111;
  font-size: 24px;
  line-height: 37px;
  margin-top: 6px;
  margin-bottom: 15.5px;

  @media (min-width: @tablet-width) {
    font-size: 36px;
    text-align: left;
    min-width: 250px;
    margin-bottom: 24px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 30px;
    margin-bottom: 47.5px;
  }
}

.type-program__text {
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 14px;
  line-height: 18px;
  margin-bottom: 5px;
  padding-bottom: 22px;
  border-bottom: 1px solid #d9d9d9;

  @media (min-width: @tablet-width) {
    font-size: 16px;
    line-height: 24px;
    border-bottom: none;
    margin-bottom: 30px;
  }

  @media (min-width: @desktop-width) {
    padding-bottom: 0;
    margin-bottom: 7px;
  }
}

.type-program__link {
  position: relative;
  display: block;
  text-transform: uppercase;
  font-size: 16px;
  line-height: 16px;
  color: #111111;
  margin-top: 9px;
  margin-bottom: 9px;
  padding-top: 4px;
  padding-bottom: 4px;

  @media (min-width: @tablet-width) {
    font-size: 20px;
    line-height: 20px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 8px;
  }
}

.type-program__link--arrow::after {
  content: "";
  display: inline-block;
  width: 24px;
  height: 11px;
  margin-left: 12px;
  margin-bottom: 1px;
  padding-top: 4px;
  padding-bottom: 4px;

  background-image: url("../img/icon-right-arrow-long.svg");
  background-repeat: no-repeat;

  @media (min-width: @tablet-width) {
    margin-left: 23px;
    margin-bottom: 3px;
  }
}

.type-program__link--arrow:focus::after,
.type-program__link--arrow:hover::after {
  width: 33px;
  height: 11px;
  background-image: url("../../img/icon-right-arrow-verylong.svg");
  background-repeat: no-repeat;
  transition-duration: 100ms;
}

.strategy__link:active {
  opacity: 0.4;

  &::after {
    opacity: 0.4;
  }
}
