.live-example {
  background: #f2f2f2;
  padding-top: 9px;
  padding-bottom: 33px;

  @media (min-width: @tablet-width) {
    background: transparent;
    padding-top: 35px;
    padding-bottom: 0;
  }

  @media (min-width: @desktop-width) {
    padding: 0;
    background: linear-gradient(#ffffff 30%, #f2f2f2 30%);
  }
}

.live-example__flex {
  @media (min-width: @desktop-width) {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    max-width: 1300px;
    margin: 0 auto;
  }
}

.live-example__title {
  margin-top: 8px;
  margin-bottom: 10px;

  @media (min-width: @tablet-width) {
    margin-bottom: 51px;
  }

  @media (min-width: @desktop-width) {
    display: block;
    padding-top: 55px;
    padding-bottom: 20px;
  }
}

.live-example__wrapper {
  margin: 0;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
  }

  @media (min-width: @desktop-width) {
    width: 50%;
    min-height: 500px;
  }
}

.live-example__text {
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 14px;
  line-height: 18px;
  padding-right: 5px;

  @media (min-width: @tablet-width) {
    width: 100%;
    font-size: 16px;
    line-height: 24px;
    padding-right: 15px;
    margin-bottom: 50px;
  }

  @media (min-width: @desktop-width) {
    padding-right: 180px;
  }
}

.live-example__list {
  display: flex;
  justify-content: space-between;
  .list-reset();
  margin-bottom: 30px;

  @media (min-width: @tablet-width) {
    justify-content: flex-start;
  }
}

.live-example__list-item {
  position: relative;
  text-align: center;
  min-width: 124px;
  border: 1px solid #cdcdcd;
  border-radius: 4px;
  margin-right: 32px;

  @media (min-width: @tablet-width) {
    min-width: 162px;
    height: 72px;
  }
}

.live-example__field {
  margin-top: 15px;
  font-size: 24px;
  line-height: 24px;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    line-height: 37px;
    margin-bottom: 20px;
  }
}

.live-example__value-1,
.live-example__value-2 {
  position: absolute;
  display: block;
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 12px;
  line-height: 12px;
  width: 77px;
  top: 50px;
  left: 22px;
  background: #f2f2f2;
  padding: 0 5px;
  margin: 0;

  @media (min-width: @tablet-width) {
    background: #ffffff;
    font-size: 14px;
    line-height: 14px;
    top: 62px;
  }

  @media (min-width: @desktop-width) {
    background: #f2f2f2;
  }
}

.live-example__value-1 {
  @media (min-width: @tablet-width) {
    width: 114px;
    left: 22px;
  }
}

.live-example__value-2 {
  @media (min-width: @tablet-width) {
    width: 142px;
    left: 9px;
  }
}

.live-example__cost {
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    width: 180px;
    margin-left: auto;
    font-size: 20px;
    text-align: right;
  }

  @media (min-width: @desktop-width) {
    width: 100%;
    text-align: left;
    align-self: center;
    margin-bottom: 40px;
  }
}
