.select-program::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  height: 64%;
  z-index: -1;
  background-color: #65a93c;
  background-image: url("../img/index-background-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}
