  .socials__icon-insta {
    width: 23px;
    height: 23px;
    margin-top: 1px;
    margin-left: 4px;
  }
}
.socials__icon-fb {
  width: 10px;
  height: 19px;
  margin-top: 1px;
  margin-left: 5px;
}
@media (min-width: 768px) {
  .socials__icon-fb {
    width: 14px;
    height: 28px;
    margin-top: -2px;
    margin-left: 2px;
  }
}
.socials__icon-vk:hover,
.socials__icon-vk:focus,
.socials__icon-insta:hover,
.socials__icon-insta:focus,
.socials__icon-fb:hover,
.socials__icon-fb:focus {
  fill: #68b738;
  transition-duration: 0.2s;
}
.socials__academy-wrapper {
  text-align: center;
  padding-top: 20px;
}
@media (min-width: 768px) {
  .socials__academy-wrapper {
    padding-top: 15px;
  }
}
.socials__academy-link {
  display: flex;
  justify-content: space-between;
  width: 100%;
  text-decoration: none;
}
@media (min-width: 768px) {
  .socials__academy-link {
    width: 162px;
  }
}
.socials__academy-text {
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 16px;
  line-height: 16px;
  margin-top: 10px;
  margin-bottom: 10px;
  color: #444444;
}
.socials__academy-logo {
  text-align: left;
  width: 39px;
  height: 39px;
}
.socials__academy-link:focus .socials__academy-logo,
.socials__academy-link:hover .socials__academy-logo {
  fill: #68b738;
  transition-duration: 0.2s;
}
.socials__academy-link:active {
  opacity: 0.3;
}
